import {
  showPopupCellAndClose,
} from '../../press-popup-cell/demo-helper/helper';


export const local = {
  empty: false,
  isHor: false,
  isGamePrize: true,
};

export function showCustomPopup({ context, callback }) {
  showPopupCellAndClose({
    context,
    title: '',
    closeIcon: true,
    cellList: [
      {
        label: '',
        type: 'switch',
        open: local.empty,
        click: ({ context: popupContext }) => {
          popupContext.closeDialog();
          local.empty = !local.empty;
          context.onGTip('');

          if (typeof callback.changeParams === 'function') {
            callback.changeParams.call(context, local);
          }
        },
      },
      {
        label: '',
        type: 'switch',
        open: local.isHor,
        click: ({ context: popupContext }) => {
          popupContext.closeDialog();
          local.isHor = !local.isHor;
          context.onGTip('');

          if (typeof callback.changeParams === 'function') {
            callback.changeParams.call(context, local);
          }
        },
      },
      {
        label: '',
        type: 'switch',
        open: local.isGamePrize,
        click: ({ context: popupContext }) => {
          popupContext.closeDialog();
          local.isGamePrize = !local.isGamePrize;
          context.onGTip('');

          if (typeof callback.changeParams === 'function') {
            callback.changeParams.call(context, local);
          }
        },
      },
    ],
  });
}
