import PressPopup from '../press-popup/press-popup';
import PressDialog from '../press-dialog/press-dialog';
import PressButton from '../press-button/press-button';
import PressAwardPopupList from '../press-award-popup-list/press-award-popup-list';
import PressAwardPopupHor from './press-award-popup-hor';

const DEFAULT_VERT_TITLE = '';


export default {
  name: 'PressAwardPopup',
  options: {
    virtualHost: true,
    styleIsolation: 'shared',
  },
  components: {
    PressPopup,
    PressDialog,
    PressAwardPopupList,
    PressAwardPopupHor,
    PressButton,
  },
  props: {
    title: {
      type: String,
      default: DEFAULT_VERT_TITLE,
    },
    curRoleName: {
      type: String,
      default: '',
    },
    awardList: {
      type: Array,
      default: () => [],
      required: false,
    },
    isGamePrize: {
      type: Boolean,
      default: true,
    },
    tipTitle: {
      type: String,
      default: '',
    },
    confirmText: {
      type: String,
      default: '',
    },
    cancelText: {
      type: String,
      default: '',
    },
    isHor: {
      type: Boolean,
      default: false,
    },
    horTitle: {
      type: String,
      default: '',
    },
    horSubTitle: {
      type: String,
      default: '',
    },
    horConfirmText: {
      type: String,
      default: '',
    },
    horCancelText: {
      type: String,
      default: '',
    },
    horDesc: {
      type: String,
      default: '',
    },
  },
  emits: ['dimissDialog', 'onShowSwitchRoleDialog', 'onExchange', 'onConfirm', 'onCancel', 'onGoAward'],
  data() {
    return {
      isShowpopupClose: true,
      // 
      isShowPopup: false,
      // 
      isShowTipDialog: false,
    };
  },
  computed: {
    innerTitle() {
      const { title } = this;
      console.log('title', title, this.isGamePrize);
      if (title === DEFAULT_VERT_TITLE) {
        if (!this.isGamePrize) {
          return '';
        }
      }
      return title;
    },
  },
  mounted() {
    setTimeout(() => {
      // 
      if (this.awardList.length > 0) {
        this.isShowTipDialog = false;
        this.isShowPopup = true;
      } else { // 
        this.isShowPopup = false;
        this.isShowTipDialog = true;
      }
    }, this.isHor ? 0 : 1200);
  },
  methods: {
    dimissDialog() {
      this.$emit('dimissDialog');
      this.isShowPopup = false;
    },
    onShowSwitchRoleDialog() {
      this.$emit('onShowSwitchRoleDialog');
    },
    onExchange() {
      this.$emit('onExchange');
    },
    onConfirm() {
      this.isShowTipDialog = false;
      this.$emit('onConfirm');
    },
    onCancel() {
      this.$emit('onCancel');
    },
    onGoAward() {
      this.$emit('onGoAward');
    },
  },
};

